    as the picture. On smaller screens I want the text below the picture. */
  .row {
    /* This makes text appear to the right or the left of the picture on single-clocks and single-watches pages. 
    Without the margin text appears below the picture.  */
    margin-right: -50px; }
  .one-whole,
